export const sendSuccessMessage = async (ctx: MyContext) => {
    const keyboard = Markup.inlineKeyboard([
        [Markup.button.callback(' ', 'active_notifications')],
        [Markup.button.callback(' ', 'mainmenu')],
    ]);

    const message = fmt` : 
${code(' ')}: ${ctx.scene.session.notificationForm.name}
${code('  ')}: ${ctx.scene.session.notificationForm.sum}
${code('   ')}: ${ctx.scene.session.notificationForm.dateTime}
${code(' ')}: ${ctx.scene.session.notificationForm.type}`;

    try {
        await LaravelService.updateNotificationById(ctx.session.notificationForm.id, ctx.scene.session.notificationForm);
    } catch (error) {
        logger.error('Error creating notification:', error);
        await ctx.reply('    . ,  .', Markup.inlineKeyboard(defaultButtonsMenuOnly));
    }

    try {
        await ctx.editMessageText(message, {
            ...keyboard, // Spread the keyboard markup
            link_preview_options: {
                is_disabled: true
            },
        });
        await ctx.answerCbQuery(' ');
    } catch (error) {
        logger.error('Error sending autobooking message:', error);
        await ctx.reply(message, keyboard);
    }

}